
*{
    margin: 0;
    padding: 0;
}
.calendar{
    position: fixed;
    background-color: #fff;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.calendar-title{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    height: 90px;
    background-color: #17a4bd;
    color: #fff;
}
.back-icon{
    position: absolute;
    left: 0;
    height: 40px;
    width: 50px;
}
.back-icon:after{
    content: '<';
    line-height: 40px;
}
.t1{
    height: 40px;
    text-align: center;
    line-height: 40px;
}
.calendar-title li{
    list-style-type: none;
    float: left;
    width: 14%;
    line-height: 50px;
}
.calendar-body{
    margin-top: 100px;
}
.calendar-day{
    float: left;
    width: 14%;
    list-style-type: none;
    height: 50px;
    line-height: 50px;
    cursor: pointer;
}
.calendar-day.selected{
    background-color: #17a4bd;
    color: #fff;
}
.slide-left-enter{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}
.slide-left-leave-active{
    -webkit-transition: -webkit-transform .3s ease;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}
.slide-left-enter-active{
    -webkit-transition: -webkit-transform .3s ease;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
}
